import React,{Component} from 'react'
import './CartScreen.css'
const url='https://amazon1543.herokuapp.com/cart1?login1='
const carturl = 'https://amazon1543.herokuapp.com/cart1'
class CartScreen extends Component{
      constructor(){
            super()
            this.state={
                  carts:''
            }
      }
      componentDidMount(){
            console.log(this.state.carts)
            var GID=localStorage.getItem('name');
            fetch(`${url}${GID}`,{method:'GET'}).then((res)=>res.json()).then((data)=>this.setState({carts:data}))
           
      }
      product=(one,two)=>{
            
            return (one*two)
      }
      handle1Submit=()=>{
            const myOBj={
                  _id:this.state.carts._id
            }
            console.log(this.state.carts._id)
                  fetch(`${carturl}`, {
                  method: 'DELETE',
                  headers: {
                        
                        'Content-Type': 'application/json'
                  },
                  body:JSON.stringify(myOBj)
            })
                 
.then(data => 
    // this is the data we get after putting our data, do whatever you want with this data
    console.log(data) 
);
      }
      
     
       
      cart = (data)=>{
            if(data){
                  return(
                        data.map(item=>{
                              return(
                                    <>
                                    <div className='cart'>
                                                <img className='img' src={item.image1}/>
                                                <div className='name'><h1 style={{fontSize:'800%'}}>{item.name1}</h1></div>
                                                <div className ='price'><h2 style={{fontSize:'800%'}}>Price : ${item.cost1}</h2></div>
                                                <div className='authname'><h2 style={{fontSize:'800%'}}>Qty : {item.keyword1} <button style={{marginLeft:'50%',backgroundColor:'red',color:'white',}} onClick={this.handle1Submit()} >remove</button></h2></div>                                                
                                                <div className='price'><h2 style={{fontSize:'800%'}}>total price : ${this.product(item.keyword1,item.cost1)} </h2></div>  
                                                
                                    </div>
                                    </>                           
                              )
                        })
                  )
            }else{
                  return(
                      <img src='/images/loader.gif' style={{height:'320px', width:'420px',marginTop:'2%'}}/>
                      
                  )
            }
      }
      render(){
            
           
            return(
                        <>
                        <h1 style={{font:'status-bar',fontSize:'200%',color:'white',backgroundColor:'#203840',width:'100%'}}>Your Carts</h1>
                        {this.cart(this.state.carts)}
                        
                        </>
            )
      }
      
}

export default CartScreen;